import { Injectable } from '@angular/core';
import { Observable } from "rxjs";
import { tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpParams } from "@angular/common/http";
import { AuthService } from './auth.service';
import { Team } from '../interfaces/team.interface';
import { environment } from '../../../environments/environment';

@Injectable({
	providedIn: 'root'
})

export class EsportService {
  baseURL = environment.apiUrl + '/teams';
  authToken: any;
  options;

	constructor(private HTTP: HttpClient, private authService: AuthService) {  }

  // CREATE auth header
  createAuthenticationHeaders() {
    this.authService.loadToken();
    this.authToken = this.authService.authToken;
    // Headers option config
    this.options = new HttpHeaders({
      'Content-Type': 'application/json',
      'authorization': this.authToken
    })
  }

	// GET ALL TEAMS
  getTeams() {
    return this.HTTP.get(this.baseURL);
  }

	// POST Team
  addTeam(name: string, contact:string, address: string, logoTeam: File)
	: Observable<any> {
    var formData: any = new FormData();
    formData.append("name", name);
    formData.append("contact", contact);
    formData.append("address", address);
    formData.append("logo", logoTeam);

    return this.HTTP.post<Team>(`${this.baseURL}/register`, formData, {
      reportProgress: true,
      observe: "events"
    });
  }

  // PUT to verify team
  verifyTeam(isVerified: boolean): Observable<any> {
    this.createAuthenticationHeaders();
    return this.HTTP.put<Team>(this.baseURL + '/verifyTeam', isVerified, {headers: this.options})
  }

  // PUT score
  addScore(score: number): Observable<any> {
    this.createAuthenticationHeaders();
    // console.log(this.authToken);
    return this.HTTP.put<Team>(this.baseURL + '/add_score', score, {headers: this.options})
  }

}
